.JokesBoard {
	display: flex;
	width: 80%;
	height: 80%;
}

.JokesBoard-sidebar {
	background: rgba(240, 98, 146, 1);
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	width: 30%;
	text-align: center;
	box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1), inset 0 0 25px rgb(199, 67, 111);
	z-index: 2;
}

.JokesBoard-title {
	font-size: 4rem;
	margin: 2rem;
	color: white;
	font-weight: 300;
	letter-spacing: 0.6rem;
}
.JokesBoard-title span {
	font-weight: 700;
	letter-spacing: 0;
}

.JokesBoard-sidebar img {
	width: 50%;
	border-radius: 50%;
	box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
}

.JokesBoard-sidebar img:hover {
	animation-name: shake-slow;
	animation-duration: 5s;
	animation-timing-function: ease-in-out;
	animation-delay: 0s;
}

