.JokesBoard-getmore {
	font-size: 1.5rem;
	width: 60%;
	border-radius: 2rem;
	padding: 1rem 2rem;
	color: white;
	font-weight: 700;
	margin: 2rem 2rem 1rem;
	border: none;
	word-spacing: 0.2rem;
	cursor: pointer;
	transition: 0.8s cubic-bezier(0.2, 1, 0.2, 1);
	outline: none;
	background: linear-gradient(
		135deg,
		rgba(152, 108, 180, 0.89) 0%,
		rgba(152, 108, 180, 0.89) 50%,
		rgb(201, 226, 56) 50%,
		rgba(201, 226, 56) 100%
	);
	box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
}

.JokesBoard-sort {
	font-size: 1rem;
	width: 90%;
	border-radius: 1rem;
	padding: 1rem;
	color: white;
	font-weight: 400;
	margin: 1rem;
	border: none;
	word-spacing: 0.2rem;
	cursor: pointer;
	transition: 0.8s cubic-bezier(0.2, 1, 0.2, 1);
	outline: none;
	background: #435a61;
	box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
}

.JokesBoard-getmore:hover,
.JokesBoard-sort:hover {
	transform: translateY(-2px);
	box-shadow: 0 15px 30px rgba(0, 0, 0, 0.25);
}

.JokesBoard-main-content {
	display: flex;
	height: 90%;
	background: white;
	align-self: center;
	width: 70%;
	overflow: scroll;
	box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
	justify-content: center;
}

